<template>
  <div class="workshop-state">
    <div class="state-container">
      <ul class="status-box">
        <li class="working">生产中</li>
        <li class="stoping">停产中</li>
        <li class="notwork">非生产</li>
      </ul>
      <ul class="workshop-list">
        <li v-for="(item,index) in workshopList">
          <span class="name">{{item.name}}</span>
          <ul class="status-list">
            <li :class="item.status==2?'working':''" @click="setStatus(item,2)">生产中</li>
            <li :class="item.status==3?'stoping':''" @click="setStatus(item,3)">停产中</li>
            <li :class="item.status==1?'notwork':''" @click="setStatus(item,1)">非生产</li>
          </ul>
        </li>
      </ul>
    </div>
    <ul class="btn-list">
      <li @click="resetList()">重置</li>
      <li @click="confirmList()">确认</li>
    </ul>
    <div class="mask" v-if="showMask"></div>
    <div class="mask-box" v-if="showMask">
      <p class="mask-tit">请先选择车间状态</p>
      <ul class="bottombtn-list">
        <li @click="hideWorkshop()">取消</li>
        <li @click="hideWorkshop()">确认</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'taskList',
  data () {
    return {
      workshopList:[],
      showMask:false
    }
  },
  methods:{
    getWorkshopList(that){
      var url=`${that.api}/api/workshop?page=0&size=100&isDelete=0&sort=createTime,desc`
      that.$ajax({
        method: 'GET',
        url:url
       }).then(function (response) {
         that.workshopList=response.data.data.content
       }).catch(function (error) {
          console.log(error);
       });
    },
    setStatus(item,status){
      this.workshopList.map((itemlist,index)=>{
        if(item.id==itemlist.id){
          itemlist.status=status
          return ;
        }
      })
    },
    hideWorkshop(){
      this.showMask=false
    },
    resetList(){
      var that=this
      this.workshopList.map((itemlist,index)=>{
        itemlist.status=0
      })
      that.$ajax({
        method: 'PUT',
        url:`${that.api}/api/workshop/all`,
        data:that.workshopList,
       }).then(function (response) {
         console.log(that.workshopList);
       }).catch(function (error) {
          console.log(error);
       });
    },
    confirmList(){
      var that=this;
      if(!that.isStatus(that.workshopList)){
        that.showMask=true
        return ;
      }
      that.$ajax({
        method: 'PUT',
        url:`${that.api}/api/workshop/all`,
        data:that.workshopList,
       }).then(function (response) {
         window.webkit.messageHandlers.submitClick.postMessage(JSON.stringify(response.data.data))
       }).catch(function (error) {
          console.log(error);
       });
    },
    isStatus(arr){
      return arr.find(item =>item.status!==0)
    }
  },
  created(){
    this.getWorkshopList(this)
    document.body.className='bgWhite'
  },
  beforeDestroy:function(){
    document.body.className=''
  }
}
</script>



